import '../style/main.sass'

import Vector from './lib/vector'
import Renderer from './lib/renderer'
import Camera from './lib/camera'
import Scene from './lib/scene'
import Texture from './lib/texture'
import Ellipse from './lib/ellipse'
import Rectangle from './lib/rectangle'


const camera    = new Camera({ x: 0, y: 0, z: -1 })
const scene     = new Scene({ background: 0x555555 })
const renderer  = new Renderer()

// Add the renderers dom element to a container
document.querySelector('#container').appendChild(renderer.dom)

// Create a new texture
const texture = new Texture({ fill: '#abcdef' })

const random = (min = 0, max = 0) => { 
  if(min > max)
    return random(max, min);
  if(min === 0 && max === 0)
    return random(0, 1);
  return Math.random() * (max - min) + min;
}

const elements = []

for(let i = 0; i < 5000; i++){
  elements.push( new Rectangle({ x:random(-5000,5000), y:random(-5000,5000), width: 20, height: 20 }, texture) )
}



// add the ellipse to the scene (for rendering)
scene.add( ...elements )


const update = () => {
  requestAnimationFrame(update)
  for(let element of elements){
    element.x += random(-2,2)
    element.y += random(-2,2)
  }
  //Render the scene
  renderer.render(scene, camera)
}

//start the update loop
update()


let start
renderer.dom.addEventListener('mousedown', e => {
  let x = e.offsetX || (e.pageX - renderer.dom.offsetLeft);
  let y = e.offsetY || (e.pageY - renderer.dom.offsetTop);
  start = new Vector(x, y).copy().sub(camera.position)
});

renderer.dom.addEventListener('mouseup', e => {
  start = null
});

renderer.dom.addEventListener('mousemove', e => {
  let x = (e.offsetX || (e.pageX - renderer.dom.offsetLeft))
  let y = (e.offsetY || (e.pageY - renderer.dom.offsetTop))
  if(start){
    const temp = start.copy()
    let point = new Vector(x, y).subXY(temp)
    camera.position.setXY(point)
  }
});


renderer.dom.addEventListener('mousewheel', e => {
  e.preventDefault()
  let x = (e.offsetX || (e.pageX - renderer.dom.offsetLeft))
  let y = (e.offsetY || (e.pageY - renderer.dom.offsetTop))
  const delta =  Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)));
  camera.position.z += delta * 0.2
})